import {useAuth} from '../../../contexts/AuthContext';
import React, {useEffect, useRef, useState} from "react";
import Overlay from '../../Overlay/Overlay';
import {db, fieldValue} from '../../../firebase'
import Message from './Message';
import {forEach} from "react-bootstrap/ElementChildren";


const Chat = () => {

	const message_inp = useRef();
	const send__button = useRef();
	const {currentUser} = useAuth();
	const [mess, setMess] = useState([]);
	const [loading, setLoading] = useState(false);

	const result = /[^/]*$/.exec(window.location.href)[0];


	useEffect(() => {
		db.collection("users/" + currentUser.uid + "/chats/" + result + "/messages")
			.orderBy('timestamp', 'desc')
			.onSnapshot(snapshot =>
				setMess(snapshot.docs.map(doc => ({id: doc.id, data: doc.data()})))
			);
	}, []);

	function add(messageText, user_id, path){
		db.collection('users/' + user_id + '/chats').doc(path).collection('messages').add({
			userId: currentUser.uid,
			username: currentUser.displayName,
			text: messageText,
			profilePicUrl: currentUser.photoURL,
			timestamp: fieldValue.serverTimestamp()
		})
	}
	async function getChat(user_id){
		let ans;
		await db.collection('users/' + user_id + '/chats')
			.where("with", "==", currentUser.uid).get()
			.then((qs)=>
			{qs.forEach((doc)=>{ans =doc.id})})
		return ans
	}

	async function sendMessage() {
		try {
			setLoading(true);
			let target
			let messageText = message_inp.current.value;
			await db.collection('users/' + currentUser.uid + '/chats')
				.doc(result)
				.get()
				.then((doc)=>{
				target = (doc.data().with)
				}
			)
			//getChat(target)
			let a = getChat(target)
			console.log(a)
			//add(messageText, currentUser.uid, result)
			//add(messageText, target, getChat(target))
			}
		catch {
			console.log('smth went wrong')
		}
		message_inp.current.value = '';
		setLoading(false);
	}

	return (
		<div>
			<Overlay/>
			<div>
				{mess.map(one => (
					<Message
						key={one.id}
						text={one.data.text}
						profilePicUrl={one.data.profilePic}
						timestamp={one.data.timestamp}
						userId={one.data.userId}
						username={one.data.username}
					/>
				))}
			</div>
			<textarea ref={message_inp} rows="5"/>
			<button ref={send__button} type="button" onClick={sendMessage}>Отправить</button>
		</div>
	);
}

export default Chat



